.setting input[type="number"], 
.setting textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box; /* –£—á–∏—Ç—ã–≤–∞–µ—Ç padding –∏ border –≤ –æ–±—â–µ–π —à–∏—Ä–∏–Ω–µ —ç–ª–µ–º–µ–Ω—Ç–∞ */
