.setting .unit {
  position: absolute;
  top: 68%;
  right: 40px;
  color: #4CAF50;
  font-family: 'Inter', sans-serif;
  transform: translateY(-50%);
}

.button, .activeButton {
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
