  border-radius: 4px;
  border: none; /* –£–±–∏—Ä–∞–µ—Ç —Ä–∞–º–∫—É –≤–æ–∫—Ä—É–≥ –∫–Ω–æ–ø–∫–∏ */
  transition: none; /* –û—Ç–∫–ª—é—á–∞–µ—Ç –≤—Å–µ –∞–Ω–∏–º–∞—Ü–∏–∏ */
}

.button:focus, .activeButton:focus {
  outline: none; /* –£–±–∏—Ä–∞–µ—Ç –æ–±–≤–æ–¥–∫—É –ø—Ä–∏ —Ñ–æ–∫—É—Å–µ */
}

